import React, { Component } from 'react';
import '../styles/About.css';

// https://react.semantic-ui.com/


class About extends Component {

  render() {
    return (
      <div className="about" id="about">
        <h2>Quem somos</h2>
        <p>
          Empresa construtora de sites nova no mercado, composta por profissionais com experiência na área, buscando realizar serviços de qualidade
          de forma acessível aos seus clientes. Fornecemos construção, manutenção e suporte para você levar a imagem do seu negócio local para o mundo através da internet.
            </p>
      </div>
    );
  }
}

export default About;