import '../styles/Footer.css';
import '../styles/Home.css';
import { Link } from 'react-router-dom';
import image from '../images/seo.jpg';
import logo from '../images/bottlogo.png';

function App() {
  document.title = 'Bott Digital - Criação de sites';
  return (
    <div className="App">

      <div className="cabecalho">
        <img src={logo} className="App-logo" alt="logo" />
        <p className="subTitle">
          Soluções digitais para a sua empresa.
        </p>
      </div>

      <div className="about" id="about">
        <div className="content">
          <h2>Busca orgânica</h2>
          <p>
            Ajustamos seu site utilizando o processo de busca orgânica, método que faz com que seu site fique em evidencia na busca simples por palavras chaves nos buscadores mais utilizados como google.

        </p>
          <img src={image} className="image-content" alt="Criação de Websites." />
          <br></br>
          <br></br>

          <Link class="btn btn-secondary btn-read-more" to={'/'}> Voltar </Link>
        </div>
      </div>
    </div >
  );
}

export default App;
