import '../styles/Footer.css';
import '../styles/Home.css';
import { Link } from 'react-router-dom';
import image from '../images/analytics.jpg';
import logo from '../images/bottlogo.png';

function App() {
  document.title = 'Bott Digital - Criação de sites';
  return (
    <div className="App">

      <div className="cabecalho">
        <img src={logo} className="App-logo" alt="logo" />
        <p className="subTitle">
          Soluções digitais para a sua empresa.
        </p>
      </div>

      <div className="about" id="about">
        <div className="content">
          <h2>Analytics</h2>
          <p>
            Utilizamos a ferramenta analytics do google, ferramenta que monitora o trafego do seu site, fornecendo dados como número de visitas e áreas mais visitadas. Possibilitando uma maior exatidão na hora de corrigir e otimizar o seu site.
        </p>
          <img src={image} className="image-content" alt="Criação de Websites." />
          <br></br>
          <br></br>

          <Link class="btn btn-secondary btn-read-more" to={'/'}> Voltar </Link>
        </div>
      </div>
    </div >
  );
}

export default App;
