import React, { Component } from 'react';
import '../styles/Navbar.css';
import xTudo from '../images/hamburguer.svg';
import cancel from '../images/cancel.svg';


// https://react.semantic-ui.com/


function isChecked() {
  const checkbox = document.getElementById("checkbox").checked;

  switch (checkbox) {
    case true:
      document.getElementById("logo").style.display = 'none';
      document.getElementById("cancel").style.display = 'block';
      break;
    default:
      document.getElementById("logo").style.display = 'block';
      document.getElementById("cancel").style.display = 'none';
      break;
  }
}

class Navbar extends Component {

  render() {
    return (
      <section className="navbar">
        <nav id="navbar" className="navbar" role="navigation">
          <input id="checkbox" type="checkbox" onChange={isChecked} />
          <div className="menuDiv">
            <label className="icon" for="checkbox">
              <img id="logo" width="100%" src={xTudo} alt="icon hamburger" />
              <img style={{ display: "none" }} id="cancel" width="100%" src={cancel} alt="icon Cancel" />
            </label>
          </div>
          <div className="navegacao">
            <li className="font"><a href="/">Bott Digital</a></li>
            <li className="font"><a href="/">Home</a></li>
            <li className="font"><a href="#about">Sobre</a></li>
            <li className="font"><a href="#diferenciais">Diferenciais</a></li>
            <li className="font"> <a href="#services">Serviços prestados</a></li>
            <li className="font"><a href="#contact">Contato</a></li >
          </div >
        </nav >
      </section >
    );
  }
}

export default Navbar;