import React, { Component } from 'react';
import '../styles/Footer.css';
import insta from '../images/insta.svg';
import fb from '../images/fb.svg';
import lin from '../images/lin.svg';



// https://react.semantic-ui.com/


class Footer extends Component {

  render() {
    return (
      <footer class="footer text-center">
        <div className="row">
          <a href='https://www.facebook.com/bottdigital' target="_blank" rel="noreferrer">
            <div className="service-icon-column" >
              <img src={fb} className="social-link" alt="Facebook" />
            </div>
          </a>
          <a href='https://www.instagram.com/bottdigital/'>
            <div className="service-icon-column" >
              <img src={lin} className="social-link" alt="Linkedin" />
            </div>
          </a>
          <a href='https://www.instagram.com/bottdigital/' target="_blank" rel="noreferrer">
            <div className="service-icon-column" >
              <img src={insta} className="social-link" alt="Instagram" />
            </div>
          </a>
          <br></br>
          <br></br>
          <br></br>
          <br></br>
          <br></br>
          <p className="footer-text ">Copyright &copy; Bott Digital 2021 </p>
        </div>
      </footer>
    );
  }
}

export default Footer;