import '../styles/Footer.css';
import '../styles/Home.css';
import { Link } from 'react-router-dom';
import sitecreate from '../images/sitecreate.jpg';
import logo from '../images/bottlogo.png';



function App() {
  document.title = 'Bott Digital - Criação de sites';
  return (
    <div className="App">

      <div className="cabecalho">
        <img src={logo} className="App-logo" alt="logo" />
        <p className="subTitle">
          Soluções digitais para a sua empresa.
        </p>
      </div>

      <div className="about" id="about">
        <div className="content">
          <h2>Criação de sites</h2>
          <p>
            Criamos sites utilizando a tecnologia HTML5 seguindo as principais regras de padronização mais adotadas na web, auxiliando na velocidade de carregamento e indexação nos principais mecanismos de busca. Originando um maior número de visitantes mês bem como divulgação direta e indireta de sua marca/empresa.
        </p>
          <img src={sitecreate} className="image-content" alt="Criação de Websites." />
          <br></br>
          <br></br>

          <Link class="btn btn-secondary btn-read-more" to={'/'}> Voltar </Link>
        </div>
      </div>
    </div >

  );
}

export default App;
