import React, { useState } from 'react';
import '../styles/Form.css';
import '../styles/Inputs.css';
import axios from 'axios';
import loading from '../images/loading.gif';


//////////
function Form() {
  const [campos, setCampos] = useState({
    name: '',
    email: '',
    phone: '',
    mensagem: '',
  });

  function handleInputChange(event) {

    campos[event.target.name] = event.target.value;
    setCampos(campos);
  }

  function send() {
    const formData = new FormData();
    Object.keys(campos).forEach(key => formData.append(key, campos[key]));
    axios.post('https://bott-digital-api.herokuapp.com/send',
      formData,
      {
        headers: {
          "Content-Type": `multipart/form-data; boundary=${formData._boundary}`,
          "Access-Control-Allow-Origin": "*"
        }
      })
      .then(response => { console.log("Tudo certo ", response.data); })
  }

  function handleFormSubmit(event) {
    event.preventDefault();
    document.getElementById("loading").style.visibility = 'visible';
    document.getElementById("sendMessageButton").disabled = true;
    send(campos);
    setTimeout(() => {

      document.getElementById("loading").style.visibility = 'hidden';
      document.getElementById("sendMessageButton").disabled = false;
      alert("Mensagem enviada");
    }, 3000);


  }
  return (

    <section class="contact-form-main" id="contact">
      <h2>CONTATE-NOS</h2>
      <p>Entre em contato
      através do nosso
          e-mail.</p>
      <form onSubmit={handleFormSubmit}>

        <div className="form-area">
          <input id="name" name="name" type="text" placeholder="Seu nome" required="required" onChange={handleInputChange} />


          <input id="email" name="email" type="email" placeholder="Seu E-mail" required="required" onChange={handleInputChange} />


          <input class="form-control" id="phone" name="phone" type="tel" placeholder="Seu Telefone" required="required" onChange={handleInputChange} />

        </div>

        <div className="form-area">
          <textarea class="form-control" id="mensagem" name="mensagem" placeholder="Sua Mensagem" required="required" onChange={handleInputChange}></textarea>
          <p class="help-block text-danger"></p>
        </div>
        <img className="loading" width="100%" height="100%" id="loading" src={loading} alt="logo" />
        <div className="text-center" >
          <button className="btn-submit" id="sendMessageButton" type="submit">Enviar</button>
        </div>
      </form>
    </section>
  )
}

export default Form;